import Vue from 'vue'
import Vuex from 'vuex'
import user from './modules/user'
import product from './modules/product'
import wx from './modules/wx'
import url_prefix from './modules/url_prefix'

Vue.use(Vuex)

const store = new Vuex.Store({
  modules: {
      user,
      product,
      wx,
      url_prefix
  }
})

export default store
